import React, { useRef } from 'react'
import emailjs from '@emailjs/browser';
import { gsap } from 'gsap';

const Contact = () => {
    const form = useRef()
    const nameInput = useRef()

    const sendEmail = (e) => {
        e.preventDefault();

        emailjs.sendForm('service_14tu68a',
         'template_0nduef6',
         form.current, 
         'ndVT_EB_jV7bGsfD9')
            .then((result) => {
                console.log(result.text);
            }, (error) => {
                console.log(error.text);
            });
            e.target.reset( )
            gsap.to('.Complete', {
                display: "flex"
            })

            setTimeout(() => {
                gsap.to('.Complete', {
                    display: "none"
                })
            }, 1000)
  
    };

    return (
        <div className='contactCons'>
            <div className='contactText'>
                REACH ME OUT!
            </div>
            <div className='Complete'>
                Thank you for reaching me! i'll reply as soon as i can.<ion-icon name="checkmark-outline"></ion-icon>
            </div>
            <form ref={form} onSubmit={sendEmail}>
                <input  ref={nameInput} type='text' placeholder='full name' name='user_name' required></input>
                <input type='email' placeholder='Email' name='user_email' required></input>
                <input type='text' placeholder='Subject' name='subject'></input>
                <textarea placeholder='your message' name='message'></textarea>
                <button className='sendBtn' type='submit'>send</button>
            </form>
        </div>
    )
}

export default Contact
